
.page_home_div1{
    margin-inline: 5vw;
}

.page_home_div1 p{
    font-size: 1rem;
    margin-top: 1.5rem;
    width: 80%;
}

.page_home_div1 p > span{
    color: #7843E9;
    font-weight: 700;
}

.contacts-flex {
    display: flex;
    align-items: center;
    justify-content: start;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
}

.line {
    width: 10%;
    height: 0.7px;
    background-color: #ABB2BF;
    margin-right: 5px;
}

.icon {
    width: 24px;
    height: 24px;
}
.img_lilia{
    width: 100%;
}
.page_home{
    z-index: -50;
}


@media screen and (min-width:700px) {
    .page_home{
        position: relative;
        display: flex;
        flex-direction: row;
    }
    .contacts-flex{
        position: absolute;
        bottom: 0;
    }
    .page_home_div1{
        width: 50%;
    }
    .img_lilia{
        z-index: 0;
    }
    .line {
        width: 30vw;
    }
    .page_home_div1{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

@media screen and (min-width:900px) {
    .page_home{
        padding-inline: 5vw;
    }
    .contacts-flex{
        left: -5vw;
    }
}