header{
    background-color: #fafafa;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    height: 10vh;
    position: fixed;
    padding: 5px calc(20px + 5vw);
    border-bottom: 1px solid #7843E9;
    z-index: 1000;
}

.list{
    display: flex;
}
.list-link{
    text-decoration: none;
    color: #111111;
    margin-left: 1.5rem;
    font-size: 1.25rem;
    font-weight: 600;
    transition: 0.3s ease-in-out;
}
.list-link:hover{
    color: #7a54e8;
}

.home_icon{
    width: 40px;
    height: 40px;
}
.img_menu_mobile{
    display: none;
    width: 21px;
    height: 20px;
}
.img_menu_mobile_active{
    display: none;
}

.language-selector {
    margin-left: 1.5rem;
    font-size: 1.25rem;
    font-weight: 600;
}

.split-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    font-size: inherit;
    font-weight: inherit;
    padding: 0;
}

.dropdown {
    margin-left: 0.5rem;
    font-size: inherit;
    border: none;
    background-color: transparent;
    cursor: pointer;
    margin: 0;
}


@media screen and (max-width:780px) {
    .img_menu_mobile{
        display: block;
    }
    .img_menu_mobile_active{
        display: block;
        width: 21px;
        height: 20px;
        position: fixed;
        z-index: 200;
        left: 55vw;
    }
    .list{
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top:0;
        right: 0;
        padding-top: 90px;
        padding-left: 10px;
        font-size: 1.15rem;
        font-weight: 700;
        width: 50vw;
        height: 100%;
        background-color: #f1e9fd;
        z-index: 10;
        transition: 0.3s ease-in-out;
    }
    .hidelistmenu{
        right: -50vw;
    }
    .list-link{
        margin-bottom: 1.5rem;
        border-bottom: 1px solid #7843E9;
    }
    .language-selector {
        margin-bottom: 1.5rem;
        border-bottom: 1px solid #7843E9;
    }
    .split-button{
        margin-bottom: 0.25rem;
    }
}