.page_certifications{
    margin-inline: 5vw;
    margin-bottom: 2rem;
}

.certif{
    margin-top: 2rem;
    /* background-color: aqua; */
    width: 90%;
    margin-inline: auto;
}

.certif > p{
    margin: 0;
}

.certif > h2{
    color: #7843E9;
}

.certif > ul{
    margin: 0;
}

.verify-p{
    text-align: right;
}

.certif > img{
    margin-top: 0.5rem;
    border-radius: 20px;
    border: 2px solid #7843E9;
}

@media screen and (min-width:800px) {
    .certif{
        width: 80%;
    }
}