.page_projet{
    margin-inline: 5vw;
    margin-bottom: 2rem;
}

.projet{
    margin: 2rem 0;
    background-color: #F2EBFD;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
    padding-bottom: 1rem;
}

.projet > video{
    width: 100%;
    margin: 0.5rem 0;
    margin-top: 0;
    border-radius: 10px 10px 0px 0px;
}

.projet_info > h2{
    text-align: center;
    margin: 0.5rem;
    font-size: 1.25rem;
    color: #7843E9;
    font-weight: 700;
}

.projet_info > p{
    margin: 0.5rem;
    text-align: center;
    width: 85%;
    margin-inline: auto;
}

.projet_info > .outils{
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
    width: 70%;
    justify-content: center;
    margin-inline: auto;
}

.projet_info > .outils > .outil{
    background-color: black;
    color: #F2EBFD;
    padding: 0 5px;
    border-radius: 10px;
    font-size: 0.9rem;
}

.view{
    display: flex;
    align-items: center;
}

.view > div{
    font-size: 0.8rem;
}

.projet_info img{
    width: 35px;
}

.links{
    margin-top: 1rem;
    padding-inline: 1rem;
    display: flex;
    justify-content: space-around;
}

@media screen and (min-width:700px){
    .projet{
        width: 80vw;
        margin-inline: auto;
        display: flex;
        justify-content: space-between;
        padding: 10px 15px;
    }
    .projet > video{
        width: 60%;
        margin: 0;
        border-radius:10px;
    }
    .projet_info{
        width: 40%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

