form{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 35rem;
    width: 80vw;
    background-color: #F2EBFD;
    padding: 10px;
    border-radius: 10px;
}

form > div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 7px;
}

.page_certifications h2{
    margin-top: 2rem;
    margin-bottom: 2rem;
    /* color: #7843E9; */
}

.contact{
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contact_g{
    width: 40px;
}
.contact_l{
    width: 35px;
}

.input{
    margin-bottom: 1rem;
    color: #21243D;
}

input[type="text"],
input[type="email"],
textarea {
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button[type="submit"] {
  padding: 10px;
  background-color: #7843E9;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #5f21e5;
}

@media screen and (min-width:500px){
    form{
        width: 400px;
    }
}

@media screen and (min-width:800px){
    form{
        height: 28rem;
        width: 600px;
    }
    textarea{
        height: 10rem;
    }
    .page_certifications h2{
        margin-bottom: 1rem;
    }
    .contact{
        margin-top: 1rem;
    }
}